* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  /* font-family: "Roboto", sans-serif; */
  background-color: #0a0a0a;
  color: #ffffff;
}

ol, ul {
  list-style: none;
}

a {
  text-decoration: none;
}

///

.container {
  text-align: center;
  padding: 50px;
}

.headline {
  display: inline-block;
}

.animated-text {
  opacity: 0;
  animation: complexAnimation 4s ease-in-out infinite alternate;
}

// 
@keyframes complexAnimation {
  0% {
    opacity: 1;
    // transform: translateY(8%) rotate(0) scale(1);
    color: #3498db;
  }
  20% {
    opacity: 1;
    // transform: translateY(-8%) rotate(0deg) scale(1);
    color: #e74c3c;
  }
  40% {
    opacity: 1;
    // transform: translateY(8%) rotate(0) scale(1);
    color: #2ecc71;
  }
  60% {
    opacity: 1;
    // transform: translateY(-8%) rotate(0deg) scale(1);
    color: #f39c12;
  }
  80% {
    opacity: 1;
    // transform: translateY(8%) rotate(0) scale(1);
    color: #3498db;
  }
  100% {
    opacity: 1;
    // transform: translateY(-8%) rotate(0deg) scale(1);
    color: #1abc9c;
  }
}

h1 {
  margin: 0;
  font-family: 'orpheus-pro', sans-serif;
  font-size: 2em;
  transition: color 0.5s;
}



/// 

.body.isScroll {
  overflow: hidden;
}

.body {
  font-family: 'orpheus-pro', sans-serif;
  height: 3200px;
}

.headline {
  text-align: center;
  margin-top: 70px;
  margin-bottom: 100px;
  font-size: 200%;
}

.main {
  text-align: center;
}

.mainbody {
  text-align: center;
}

.body1 {
  position: absolute;
  filter: brightness(60%) contrast(130%);
  width: 100%;
  height: 1081px;
}

.body2 {

  position: relative;
  left: 70%;
  top: 200px;
  transform: translateX(-50%);
  width: 400px;
  font-size: 150%;
}

.body2 p{
  font-family: 'Poppins', sans-serif;
  font-weight: 100;
}

@media (max-width: 768px) {
  .body {
    font-family: 'orpheus-pro', sans-serif;
    height: 4200px;
  }

  .mainbody {
    padding-bottom: 0;
  }

  .body1 {
    filter: brightness(60%) contrast(130%);
    // height: 1100px;
    width: 100%;
    position: relative;
    margin: 0;
    height: 950px;
  }

  .body2 {
    padding-top: 20px;
    left: 50%;
    position: relative;
    transform: translateX(-50%);
    width: 80%;
    font-size: 100%;
    margin: 0;

  }
}


.myPhoto1 {
  background-image: url(../images/photo1.png);
}


